.cart{
    margin-top: 100px;

}

.cart-items-title{
    display: grid;
    grid-template-columns: 1fr 1.5fr 1fr 1fr 1fr 0.5fr;
    align-items: center;
    color: gray;
    font-size: max(1vw,12px);
}

.cart-items-item{
    margin: 10px 0px;
    color: black;
}

.cart-items-item img{
    width: 100px;
    border-radius: 4px;
}

.cart hr{
    height: 1px;
    background-color: #e2e2e2;
    border: none;
}

.cart-items-item .cross{
    cursor: pointer;
    font-size: 19px;
    color: red;
    background-color: rgba(0, 0, 0, 0.677);
    width: 30px;
    text-align: center;
    border-radius: 50px;
}

.cart-bottom{
    margin-top: 80px;
    display: flex;
    justify-content: space-between;
    gap: max(12vw, 20px);
    width: 40vw;
}

.cart-total{
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.cart-total-details{
    display: flex;
    justify-content: space-between;
    color: #555;
}

.cart-total hr{
    margin: 10px 0px;
}

.cart-total button{
    border: none;
    color: white;
    background-color: tomato;
    width: max(15vw,200px);
    padding: 12px 0px;
    border-radius: 4px;
    cursor: pointer;
    font-weight: 600;

}

@media (max-width:750px) {
    .cart-bottom{
        flex-direction: column-reverse;
        width: 80vw;
    }

    .cart-items-item img{
        width: 50px;
        border-radius: 2px;
    }

}